.tb_hotspot_dot {
  position: absolute;
  z-index: 6;
  top: 0;
  left: 0;
  text-decoration: none !important;
  outline: none !important;
}

.tb_hotspot__{
  font-family: Inter, sans-serif;
  border-radius: 50%;
  height: 16px;
  width: 16px;
  font-weight: 600;
  display: flex;
  cursor: pointer;
  align-items: center;
  justify-content: center;
  color: var(--tb-hotspot-color);
  background-color: var(--tb-hotspot-bg-color);
  box-shadow: 0 0 0 0 rgba(0, 0, 0, 1);
  font-size: 10px;
  transition: all .3s ease-in-out;
}

.tb_hotspot_dot:hover .tb_hotspot__,
.tb_hotspot_dot.tb_hotspot_active_ .tb_hotspot__{
  transform: scale(0.7);
  font-size: 0px;
}
.tb_hotspot_ani__{
  font-size: 0 !important;
  animation: TbPulesAnimation 2s infinite;
}

@keyframes TbPulesAnimation {
  0% {
    transform: scale(.85);
    box-shadow: 0 0 0 0 rgba(0, 0, 0, .7)
  }

  70% {
    transform: scale(1)
  }

  99% {
    box-shadow: 0 0 0 8px rgba(0, 0, 0, 0)
  }

  100% {
    transform: scale(.85);
    box-shadow: 0 0 0 0 rgba(0, 0, 0, 0)
  }
}

.subtree-container .tb_pro_tooltip{
  animation: TbFadeInAnimation .5s;
}

@keyframes TbFadeInAnimation {
  0% { opacity: 0; }
  100% { opacity: 1; }
}

.tb_pro_tooltip.react-tooltip {
  background-color: #fff;
  padding: 10px;
  border-radius: 4px;
  width: 120px;
  max-width: 120px;
  opacity: 1;
  box-shadow: 0 2px 2px rgba(0, 0, 0, 0.20);
  z-index: 9999999999999;
}

.tb_tooltip_wrap{
  text-decoration: none !important;
  outline: none !important;
  color: initial;
}

.tb_p_tooltip_title {
  font-family: var(--tb-product-font);
  color: var(--tb-product-color);
  font-weight: var(--tb-product-varient, 600);
  font-size: 12px;
  line-height: inherit;
  margin-bottom: 4px;
  text-align: left;
  text-decoration: none;
  display: block;
}

.tb_p_tooltip_price {
  font-family: var(--tb-price-font);
  color: var(--tb-price-color);
  font-weight: var(--tb-price-varient);
  font-size: 15px;
  margin-right: 3px;
  margin-bottom: 0px;
  text-align: left;
  margin-top: 0;
  white-space: nowrap;
}

.tb_p_tooltip_price.tb_price_disabled{
  text-decoration: line-through;
  font-size: 12px;
  opacity: .5;
}

.tb_pro_tooltip .react-tooltip-arrow{
  display: none;
}

.tb_pro_tooltip.react-tooltip{
  color: #000;
}

.tb_pro_price{
  font-family: var(--tb-price-font);
  color: var(--tb-price-color);
}

.tb_pro_title{
  color: var(--tb-product-color);
  font-family: var(--tb-product-font);
  color: var(--tb-product-color);
  font-weight: var(--tb-product-varient);
}
/** Hotspot CSS End **/