.tb_cc_post_container{
    display: flex;
    flex-wrap: wrap;
    width: 100%;
}

.tb_cc_post_wrapper{
    width: 25%;
    padding: 7.5px;
}

.tb_cc_post_in{
    display: block;
    position: relative;
    box-shadow:  1px 1px 5px rgba(0, 0, 0, 0.13);
    cursor: pointer;
    overflow: hidden;
    background-color: var(--tb-bg-color);
    border-radius: var(--tb-border-radius);
}

.tb_cc_image_wrap_{
    padding-bottom: 100%;
    position: relative;
}

.tb_cc_image_wrap_in{
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
}

.tb_cc_image_wrap_, .tb_cc_video_wrap_{
    padding-bottom: 100%;
    position: relative;
    background-color: rgba(0,0,0,.03);
}
.tb_cc_image, .tb_cc_video{
    height: 100% !important;
    width: 100% !important;
    position: absolute;
    left: 0;
    top: 0;
    z-index: 1;
    overflow: hidden;
    display: block;
    object-fit: cover;
    object-position: center;
}
.tb_cc_video{
    background-color: #000;
}

.tb_cc_video video,
.tb_cc_video iframe{
    object-fit: cover;
    object-position: center;
}

.tb_cc_video > div {
    display: flex;
    align-items: center;
    justify-content: center;
}

.tb_cc_image_wrap_ img{
    max-width: 100%;
    width: 100%;
    height: 100%;
    object-fit: cover;
    object-position: center;
    margin: 0 !important;
}
.tb_cc_contant_wrapper {
    padding: 10px;
}

.tb_cc_media_wrap {
    display: block;
}

.tb_cc_author {
    width: calc(100% - 30px);
}

.tb_cc_author_profile {
    height: 44px;
    width: 44px;
    border-radius: 50%;
    margin-right: 10px;
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;
    display: flex;
    flex-shrink: 0;
    overflow: hidden;
    background-color: rgba(0,0,0,.2);
}

.tb_cc_author_profile__{
    height: 100% !important;
    width: 100% !important;
    margin: 0;
    object-fit: cover;
    object-position: center;
}
.tb_cc_authorname {
    font-size: 14px;
    font-weight: 600;
    margin-bottom: 4px;
    white-space: nowrap;
    display: block;
    overflow: hidden;
    text-overflow: ellipsis;
    line-height: normal;
}

.tb_cc_authorname {
    font-family: var(--tb-author-font);
    color: var(--tb-author-color);
}

.tb_cc_username,
.tb_cc_time {
    font-family: var(--tb-font);
    color: var(--tb-author-color);
}

.tb_cc_seprator {
    background-color: var(--tb-author-color);
}

.tb_cc_content,
.tb_cc_rating_content {
    font-family: var(--tb-font);
    color: var(--tb-color);
    font-size: var(--tb-font-size);
    line-height: var(--tb-line-height);
}

.tb_cc_author {
    display: flex;
    align-items: center;
    flex-grow: 1;
}

.tb_cc_author_wrapper {
    display: flex;
    align-items: center;
}

.tb_cc_post_media_wrapp{
    position: relative;
}

.tb_cc_author_info {
    overflow: hidden;
    display: flex;
    flex-shrink: 0;
    flex-direction: column;
    width: calc(100% - 54px);
    padding-right: 10px;
}

.tb_cc_post_info {
    display: flex;
    align-items: center;
}

.tb_cc_seprator {
    height: 4px;
    width: 4px;
    min-width: 4px;
    border-radius: 50%;
    margin: 0 6px;
    opacity: 0.8;
    display: flex;
    flex-shrink: 1;
}

.tb_cc_username, .tb_cc_time {
    font-size: 10px;
    font-weight: 500;
    opacity: 0.8;
    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis;
    line-height: normal;
}

.tb_cc_username{
    max-width: 65%;
}

.tb_cc_time{
    max-width: 35%;
}

.tb_cc_social_ {
    font-size: 24px;
    height: 24px;
    margin: 6px 0;
    display: inline-block;
}

.tb_cc_social_ico{
    color: var(--tb-icon-color);
}

.tb_cc_content, .tb_cc_rating_content {
    font-size: 14px;
    line-height: 1.5;
    margin-top: 10px;
    word-break: break-word;
    white-space: pre-line;
}

.tb_cc_contant_wrapper .tb_cc_rating_content_review{
    margin-top: 0;
}

.tb_cc_content{
    text-align: left;
}

.tb_cc_content.tb_cc_text_center{
    text-align: center;
}

.tb_cc_content .tb_text_links_, .tb_cc_rating_content .tb_text_links_ {
    color: inherit !important;
    text-decoration: underline !important;
    display: inline;
}

.tb_cc_rating_content, .tb_cc_text_post {
    text-align: center;
}

.tb_cc_text_decoration::before{
    content: "\e909";
    font-family: 'WebSocialFonts';
    margin-right: 12px;
}

.tb_cc_text_decoration::after{
    content: "\e90a";
    font-family: 'WebSocialFonts';
    margin-left: 12px;
}

.tb_cc_post_in:hover .tb_share_wrapper{
    opacity: 1;
    visibility: visible;
}
.tb_cc_post_actions{
    padding: 10px;
}

.tb_cc_social_actions_ {
    display: flex;
    align-items: center;
    justify-content: space-between;
    flex-wrap: wrap;
    border-top: 1px solid rgba(0, 0, 0, 0.1);
    padding-top: 10px;
}

.tb_cc_action_counts_wrap {
    display: flex;
    padding: 3px;
    align-items: center;
    cursor: pointer;
    text-decoration: none;
    position: relative;
}
.tb_cc_action_counts {
    font-size: 12px;
    margin-left: 5px;
    font-weight: 400;
}
.tb_cc_post_cta {
    display: block;
    text-align: center;
    margin-bottom: 10px;
}

.tb_cc_post_cta_btn {
    font-family: var(--tb-font);
    padding: var(--tb-cta-padding);
    color: var(--tb-cta-color);
    background-color:  var(--tb-cta-bg-color);
    border-radius: var(--tb-cta-border-radius);
    font-weight: var(--tb-cta-font-weight);
    border-color: var(--tb-cta-border-color);
    transition: var(--tb-cta-transition);
    min-height: var(--tb-cta-height);
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 14px;
    line-height: normal;
}

.tb_cc_post_cta_btn:hover {
    color: var(--tb-cta-color-hover);
    background-color: var(--tb-cta-bg-color-hover);
    border-color: var(--tb-cta-border-color-hover);
}

.tb_cc_share_wrapper {
    position: relative;
}

.tb_cc_share_wrapper_dropdown {
    position: absolute;
    bottom: 100%;
    width: 120px;
    right: 0;
    opacity: 0;
    visibility: hidden;
    padding-bottom: 5px;
    z-index: 1;
    transform: translateY(5px);
    transition: all .2s ease-in-out;
}

.tb_cc_action_counts_wrap:hover .tb_cc_share_wrapper_dropdown, 
.tb_cc_action_counts_wrap:focus .tb_cc_share_wrapper_dropdown{
    opacity: 1;
    visibility: visible;
    transform: translateY(0px);
}


.tb_cc_share_icon_list{
    background-color: #2b2b2b;
    border-radius: 3px;
    display: flex;
    flex-direction: column;
    position: relative;
}

.tb_cc_share_icon_list:after {
    content: '';
    position: absolute;
    transform: rotate(45deg);
    background-color: #2b2b2b;
    width: 10px;
    height: 10px;
    bottom: -5px;
    right: 12px;
    border-radius: 1px;
    z-index: 0;
}

.tb_cc_share_list_in {
    display: flex;
    align-items: center;
    padding: 8px 14px;
    border-bottom: 1px solid #545454;
    transition: all .3s ease-in-out;
}

.tb_cc_share_list_in:hover{
    background-color: #333333;
}

.tb_cc_share_list_in:last-child{
    border-bottom: none;
}

.tb_cc_share_ico__ {
    font-size: 13px;
    margin-right: 5px;
    color: #cccccc;
}

.tb_cc_share_ico_txt{
    font-size: 12px;
    color: #cccccc;
}

.tb_cc_social_action__ .tb_social_action__{
    padding-top: 10px;
}

.tb_cc_social_action__ {
    display: flex;
    align-items: stretch;
    justify-content: space-around;
    border-top: 1px solid rgba(0,0,0,.1);
    padding: 6px;
}

.tb_cc_social_action__list {
    display: flex;
    justify-content: center;
    position: relative;
}

.tb_cc_social_action__list:last-child{
    border-right: none;
}

.tb_cc_social_action__ico_wrap {
    display: flex;
    align-items: center;
}

.tb_cc_social_action_counts__ {
    margin-left: 5px;
    font-size: 12px;
    font-weight: 400;
}

.tb_cc_social_action_ico__{
    font-size: 14px;
}

.tb_cc_social_action_ico__,
.tb_cc_social_action_counts__,
.tb_cc_action_counts,
.tb_cc_action_ico{
    color: var(--tb-author-color);
}